/*
* @subsection   RD Navbar
*
* @description  Describes style declarations for RD Navbar extension
*
* @author       Evgeniy Gusarov
* @link         https://ua.linkedin.com/pub/evgeniy-gusarov/8a/a40/54a
*/
@import "rd-navbar_includes/_rd-navbar-mixins.scss";


/*
* @subsection General Styles
*/

// RD Navbar Basics
%rd-navbar-transition {
  transition: .3s all cubic-bezier(0.785, 0.135, 0.150, 0.860);
}

%rd-navbar-hidden {
  display: none;
}

%rd-navbar-visible {
  display: block;
}

.rd-navbar--no-transition {
  &, * {
    transition: none !important;
  }
}

.rd-navbar-wrap {
  @extend %rd-navbar-transition;
}

.rd-navbar, .rd-navbar.rd-navbar--is-clone {
  @extend %rd-navbar-hidden;
}

.rd-navbar-fixed,
.rd-navbar-static,
.rd-navbar-fullwidth,
.rd-navbar-sidebar {
  @extend %rd-navbar-visible;
}

.rd-navbar-wrap,
.rd-navbar,
.rd-navbar-brand,
.rd-navbar-slogan,
.rd-navbar-dropdown,
.rd-navbar-megamenu,
.rd-navbar-collapse-items,
.brand-name,
.rd-navbar-nav,
.rd-navbar-panel,
.rd-navbar-search-form-input,
.rd-navbar-search-form-submit,
.rd-navbar-search-toggle,
.rd-navbar-live-search-results,
.rd-navbar-search-form {
  transition: .3s all ease;
}

// RD Navbar States
.rd-navbar--has-sidebar {
  body {
    padding-left: 270px;
  }
}

.rd-navbar--is-stuck {
  box-shadow: $rd-navbar-shadow;
}

.rd-navbar.rd-navbar-fixed + .rd-navbar.rd-navbar--is-clone,
.rd-navbar.rd-navbar-sidebar + .rd-navbar.rd-navbar--is-clone {
  display: none;
}

@import "rd-navbar_includes/_rd-navbar-components.scss";

/*
* @subsection   Hybrid  Styles
*/

//=============    Static & Fullwidth     ================
.rd-navbar-static,
.rd-navbar-fullwidth {
  .rd-navbar-brand,
  .rd-navbar-nav > li > a,
  .rd-navbar-search-toggle,
  .rd-navbar-inner {
    position: relative;
    z-index: 2;
  }

  // RD Navbar Nav
  .rd-navbar-nav {
    > li {
      display: inline-block;

      .rd-navbar-dropdown .rd-navbar-dropdown {
        left: 100%;
        top: 0;
        z-index: 2;
        margin-left: 15px;
      }
    }

    > li > .rd-navbar-dropdown {
      display: block;
      position: absolute;
      text-align: left;
      margin-top: $rd-navbar-fullwidth-dropdown-indent;
      margin-left: -20px;
      left: 0;
      z-index: 5;
      border-top: 4px solid $brand-primary;
    }

    li.rd-navbar--has-dropdown {
      position: relative;
    }

    .rd-navbar-dropdown,
    .rd-navbar-megamenu {
      @extend %rd-navbar-transition;
      @include transform(translateY(30px));

      box-shadow: 0 0 13px -1px rgba(0, 0, 0, 0.17);
      position: absolute;
      text-align: left;
      top: 100%;
      z-index: 1;
      opacity: 0;
      visibility: hidden;
      display: block;
    }

    // Dropdown states
    .rd-navbar-submenu.focus,
    .rd-navbar-submenu.opened {
      >.rd-navbar-dropdown,
      >.rd-navbar-megamenu {
        opacity: 1;
        visibility: visible;
        display: block;
        @include transform(translateY(0));
      }
    }

  }

  // RD Navbar Dropdown
  .rd-navbar-dropdown {
    background: $rd-navbar-dropdown-background;
    width: $rd-navbar-dropdown-width;
    padding-top: 7px;
    padding-bottom: 7px;

    .rd-navbar-dropdown{
      margin-top: -7px;
    }

    > li > a {
      display: block;
      font-size: 12px;
      padding: $rd-navbar-dropdown-item-padding;
      color: $rd-navbar-dropdown-item-color;
      background: $rd-navbar-dropdown-item-background;
    }

    > li > a:hover,
    > li.focus > a {
      color: $rd-navbar-dropdown-item-hover-color;
      background: $rd-navbar-dropdown-item-hover-background;
    }
  }

  // RD Navbar Megamenu
  .rd-navbar-megamenu {
    display: table;
    table-layout: fixed;
    position: absolute;
    left: 0;
    text-align: left;
    width: $rd-navbar-megamenu-width;
    border-spacing: 31px 25px;
    margin-top: $rd-navbar-megamenu-indent;
    max-width: $rd-navbar-width;
    background: $rd-navbar-megamenu-background;
    z-index: 4;

    > li {
      position: relative;
      display: table-cell;

      > ul {
        padding: $rd-navbar-megamenu-padding;

        li + li {
          margin-top: $rd-navbar-megamenu-item-indent;
        }

        a {
          display: inline-block;
          color: $rd-navbar-megamenu-item-color;
          background: $rd-navbar-megamenu-item-background;

          &:hover {
            color: $rd-navbar-megamenu-item-hover-color;
            background: $rd-navbar-megamenu-item-hover-background;
          }
        }
      }

      & + li {
        padding-left: 10px;
        &:before {
          content: '';
          position: absolute;
          top: $rd-navbar-megamenu-gutter;
          bottom: $rd-navbar-megamenu-gutter;
          width: 1px;
          right: 100%;
          margin-right: 17px;
          background: lighten($rd-navbar-megamenu-background, 7.450980392156865%);
        }
      }
    }
  }

  // RD Navbar Clone
  &.rd-navbar--is-clone {
    display: none;
    &.rd-navbar--is-stuck {
      display: block;
    }
  }

  // RD Navbar Stuck
  &.rd-navbar--is-stuck,
  &.rd-navbar--is-clone {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 999;
    background: $rd-navbar-background;
  }


}


@import "rd-navbar_includes/_rd-navbar-static.scss";
@import "rd-navbar_includes/_rd-navbar-fullwidth.scss";
@import "rd-navbar_includes/_rd-navbar-fixed.scss";
//@import "rd-navbar_includes/_rd-navbar-sidebar.scss";

/*
* @subsection   Page boxed layout style redeclaration
*
* @description  Redefines navbar style inside boxed layout
*
* @see          ../modules/_page-layouts.scss
*/
html.boxed {
  &.rd-navbar--has-sidebar {
    body {
      padding-left: $rd-navbar-nav-min-width + 30px;
      padding-right: 30px;
    }
  }

  .rd-navbar--is-clone {
    max-width: $layout-boxed-width;
    margin-left: auto;
    margin-right: auto;
  }
}

ul ul, ul ol, ol ul, ol ol {
  padding-left: 0;
}
