//
// Custom Buttons Styles
// --------------------------------------------------

// Base styles
// --------------------------------------------------

.btn {
  transition: .3s ease-out;
  letter-spacing: 0.1em;
  display: inline-block;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 700;
  padding: $padding-small-vertical $padding-base-horizontal;

  &:focus,
  &:active,
  &:active:focus{
    outline: none;
  }


  &,
  &:active,
  &.active {
    &:focus,
    &.focus {

    }
  }

  &:hover,
  &:focus,
  &.focus {

  }

  &:active,
  &.active {
    @include box-shadow(none);
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {

  }
}


// Alternate buttons
// --------------------------------------------------

.btn-default {
  @include button-variant-custom($gray-darker, transparent, $gray, $white, $gray-darker, $gray-darker);
}

.btn-white {
  @include button-variant-custom($white, transparent, rgba($white , .78), $brand-primary, transparent, rgba($brand-primary, .78));
}

.btn-primary {
  @include button-variant-custom($white, $brand-primary, $brand-primary, $white, darken($brand-primary, 15%), darken($brand-primary, 15%));
}

.btn-nepal {
  @include button-variant-custom($color-nepal, transparent, $color-nepal, $brand-primary, transparent, $brand-primary);
}

// Success appears as green
.btn-success {

}

// Info appears as blue-green
.btn-info {

}

// Warning appears as orange
.btn-warning {

}

// Danger and error appear as red
.btn-danger {

}


// Button Sizes
// --------------------------------------------------

.btn-xs {
  padding: $padding-xs-vertical $padding-xs-horizontal;
}

.btn-sm {
  padding: $padding-small-vertical $padding-small-horizontal;
}

.btn-lg {
  padding: $padding-large-vertical $padding-large-horizontal;
}



// Button Shapes
// --------------------------------------------------

.btn-rect {
  border-radius: 0;
}


// Button Icon styles
// --------------------------------------------------

.btn{

  &.btn-icon {
    padding-left: 17px;
    padding-right: 17px;

    .icon {
      font-size: 28px;
      line-height: 28px;
      vertical-align: middle;
      transition: 0s;
    }

    &-left{
      .icon{
        float: left;
        padding-right: 10px;
      }
    }

    &-right{
      .icon{
        float: right;
        padding-left: 10px;
      }
    }

  }
}