//
// Utility classes
// --------------------------------------------------

// Helpers
// -------------------------

.img-responsive{
  @include img-responsive(inline-block);
  width: 100%;
  height: auto;
}

.opacity-50{
  opacity: .5;
}

.fz-13{
  font-size: 13px;
}

.relative{  position: relative;}

.overflow{  overflow: hidden;}

.letter-spacing-200{  letter-spacing: .2em;}

.letter-spacing-100{  letter-spacing: .1em;}

.letter-spacing-75{  letter-spacing: .075em;}

.section-cover-1{
  height: 100vh;
  max-height: 580px;

  @media (max-height: 800px) {
    max-height: 280px;
  }
}
// Components
// -------------------------

// Section position

.section-md-right {
  position: relative;

  @media (min-width: $screen-md-min) {
    &.bg-ebony-clay:before {
      content: '';
      position: absolute;
      top: 50%;
      right: 50%;
      width: 50px;
      height: 30px;
      display: block;
      border: solid transparent;
      border-right-color:  $color-ebony-clay;
      @include transform(translateY(-50%));
      z-index: 1;

      border-width: 15px 30px 15px 0 ;
    }

    .section-img {
      position: absolute;
      right: 50%;
      left: 0;
      bottom: 0;
      top: 0;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

.section-md-left {
  position: relative;

  @media (min-width: $screen-md-min) {
    &.bg-ebony-clay:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 50px;
      height: 30px;
      display: block;
      border: solid transparent;
      border-left-color:  $color-ebony-clay;
      @include transform(translateY(-50%));
      z-index: 1;

      border-width: 15px 0 15px 30px;
    }

    .section-img {
      position: absolute;
      left: 50%;
      right: 0;
      bottom: 0;
      top: 0;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

// Border custom

.border-md-left {
  padding-top: 40px;

  h3 {
    position: relative;
  }

  h3:before {
    content: '';
    height: 1px;
    width: 90px;
    display: block;
    position: absolute;
    top: -40px;
    left: 50%;
    @include transform(translateX(-50%));
    background: rgba($white, .38);
  }

  @media (min-width: $screen-md-min) {
    padding-left: 40px;

    h3:before {
      top: 8px;
      left: -40px;
      width: 1px;
      height: 90px;
      @include transform(translateX(0));
    }

    &.border-left-spacing-lg{
      padding-left: 60px;

      h3:before{
        left: -60px;
      }
    }

    &.border-left-spacing-sm{
      padding-left: 30px;

      h3:before{
        left: -30px;
      }
    }
  }
}

.icon-border-md-right {
  position: relative;
  padding-bottom: 20px;


  &:after {
    content: '';
    height: 1px;
    width: 90px;
    display: block;
    position: absolute;
    bottom: 0;
    left: 50%;
    @include transform(translateX(-50%));
    background: rgba($white, .38);
  }

  @media (min-width: $screen-md-min) {
    padding-right: 20px;
    padding-bottom: 0;

    &:after {
      top: 50%;
      left: auto;
      right: 0;
      width: 1px;
      height:110px;
      @include transform(translate(0 ,-50%));
    }
  }
}

// Headings aside

.heading-aside{
  padding-top: 40px;
  @include transform-origin(-50% -50%);

  &:before{
    content: '';
    position: absolute;
    width: 90px;
    height: 1px;
    background: rgba($gray-dark, .38);
    top: 15px;
    left: 50%;
    @include transform(translateX(-50%));
  }

  @media (min-width: $screen-lg-min) {
    position: absolute;
    left: 0;
    top: 0;

    transform-origin: 100% 0;

    @include transform(translateX(-100%) rotate(-90deg));
    padding-top: 50px;
    &:before {
      right: 0;
      width: 90px;
      height: 1px;
      @include transform(translateX(0));

    }
  }
}


// Text bordered

.text-bordered{
  position: relative;
  padding: 0 20px 20px 20px;
  border: 1px solid rgba($gray-dark , .6);
  border-top-width: 0;
  margin-top: 10px!important;

  .text-top{
    position: relative;
    @include transform(translateY(-50%));
  }

  .text-top:before,
  .text-top:after{
    content: '';
    height: 1px;
    border-top: 1px solid rgba($gray-dark , .6);
    display: block;
    position: absolute;
    width: 1000px;
    top: 50%;
    @include transform(translateY(-50%));
  }
  
  .text-top:before{
    right: calc(100% - 27px);
  }
  
  .text-top:after{
    left: calc(100% - 27px);
  }

  @media (min-width: $screen-xs-min) {
    .text-top:before,
    .text-top:after{
    }
  }
}

// Contact Info

.contact-info{
  text-transform: uppercase;
  dl{
    dt,
    dd{
      display: inline-block;
      font-weight: 400;
    }
  }

  * + dl{
    margin-top: 3px;
  }

  a:not(.btn){
    color: $gray-base;

    &:hover,
    &:focus{
      color: $brand-primary;
    }
  }
}