/*
* Fixed Layout
*/

.rd-navbar-fixed {
  display: block;

  // RD Navbar Info
  .rd-navbar-info{
    position: fixed;
    right: 0;
    top: $rd-navbar-fixed-height;
    width: 223px;
    background:$rd-navbar-background;
    padding: 10px;
    opacity: 0;
    visibility: hidden;
    @include transform(translateY(50%));
    transition: .3s;

    a.icon:hover{
      transform:  rotateY(180deg);
    }

    .icon,
    a{
      display: inline-block;
      vertical-align: middle;
    }

    dl{
      position: relative;
      margin-top: 10px;
      z-index: 10;


      a{
        font-size: 18px;
        line-height: 1;
        letter-spacing: 0.03em;
      }

      dt{
        margin-right: 10px;
      }

      dd,dt{
        float: left;
      }
    }

    &.active{
      opacity: 1;
      visibility: visible;
      @include transform(translateY(0));
    }
  }

  // RD Navbar Brand
  .rd-navbar-brand {
    @include display-flex();
    @include align-items(center);
    position: fixed;

    text-align: left;
    top: 7px;
    left: 56px;
  }

  // RD Navbar Panel
  .rd-navbar-panel {
    @include display-flex();
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    padding: $rd-navbar-fixed-padding-vertical $rd-navbar-fixed-padding-horizontal;
    height: $rd-navbar-fixed-height;
    color: $rd-navbar-fixed-panel-color;
    z-index: 999;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      box-shadow: $rd-navbar-fixed-shadow;
      background: $rd-navbar-fixed-panel-background;
    }
  }

  // RD Navbar Toggle
  .rd-navbar-toggle {
    display: inline-block;
    position: fixed;
    z-index: 111111;
    left: 0;
    top: 3px;
    height: 48px;
  }

  // RD Navbar Nav Wrap
  .rd-navbar-nav-wrap {
    @extend %rd-navbar-transition;
    position: fixed;
    left: 0;
    top: -$rd-navbar-fixed-height;
    width: $rd-navbar-fixed-nav-wrap-width;
    bottom: -$rd-navbar-fixed-height;
    background: $rd-navbar-background;
    box-shadow: $rd-navbar-fixed-shadow;

    padding: 112px 0 56px;
    z-index: 15;
    @include transform(translateX(-120%));

    &.active {
      @include transform(translateX(0));
    }
  }

  .rd-navbar-nav {
    display: block;
    height: 100%;
    overflow: auto;
    text-align: left;

    background: $rd-navbar-fixed-nav-background;
    font-size: $rd-navbar-fixed-font-size;
    line-height: $rd-navbar-fixed-line-height;
    font-weight:$rd-navbar-fixed-font-weight;

    &:before, &:after {
      content: '';
      display: block;
      height: 8px;
    }

    li {
      padding: 3px 5px;
      > a {
        display: block;
        padding: $rd-navbar-fixed-item-padding;
        color: $rd-navbar-fixed-nav-color;
        background: $rd-navbar-fixed-nav-background;
        text-transform: uppercase;
        letter-spacing: 0.1em;
        border: 1px solid transparent;
      }

      &.opened > a,
      a:hover ,
      &.active > a,
      &.focus > a,
      &.focus > a:hover{
        color: $rd-navbar-fixed-nav-hover-color;
        background: $rd-navbar-fixed-nav-hover-background;
        border-color: rgba($white , 0.63);

        >.rd-navbar-submenu-toggle {
          color: $rd-navbar-fixed-submenu-toggle-hover-color;

          &:hover {
            cursor: pointer;
            color: $rd-navbar-fixed-submenu-toggle-hover-color;
          }
        }
      }

    }
  }

  // RD Navbar Collapse
  .rd-navbar-collapse {
    &-toggle {
      @include make-toggle(
                      $rd-navbar-collapse-toggle-preset,
                      $rd-navbar-min-line-height,
                      $rd-navbar-min-font-size,
                      $rd-navbar-panel-color
      );
      display: inline-block;
      width: 56px;
      height: 56px;
      line-height: 56px;
      z-index: 999;
      position: fixed;
      top: 0;
      right: 0;
      background: transparent;
      outline: none;
      border: none;
    }
  }

  // RD Navbar Submenu/Megamenu
  .rd-navbar-dropdown,
  .rd-navbar-megamenu {
    display: none;
  }

  .rd-navbar-submenu {
    position: relative;
    @extend %rd-navbar-transition;

    .rd-navbar-dropdown li  > a,
    .rd-navbar-megamenu ul li > a{
      padding-left: 18px;
    }
    .rd-navbar-dropdown .rd-navbar-dropdown li > a,
    .rd-navbar-megamenu ul li li > a{
      padding-left: 28px;
    }
  }

  .rd-navbar-nav {

    li {
      .rd-navbar-dropdown,
      .rd-navbar-megamenu {
        transition: opacity 0.3s, height 0.4s ease;
        opacity: 0;
        height: 0;
        overflow: hidden;
      }

      &.opened {
        > .rd-navbar-dropdown,
        > .rd-navbar-megamenu {
          opacity: 1;
          height: auto;
          visibility: visible;
          display: block;
        }
        > .rd-navbar-submenu-toggle {
          &::after {
            @include transform (rotate(180deg));
            top: 3px;
          }
        }
      }
    }

    .rd-navbar-submenu-toggle {
      &::after {
        content: '\f078';
        position: absolute;
        top:2px;
        right: 0;
        margin-top: 0;
        width: 65px;
        height: 38px;
        font: 400 14px "FontAwesome";
        line-height: 38px;
        text-align: center;
        cursor: pointer;
        transition: 0.4s all ease;
        z-index: 2;
        color: $rd-navbar-fixed-submenu-toggle-color;
      }
    }

    &::-webkit-scrollbar {
      width: 4px;
    }

    &::-webkit-scrollbar-thumb {
      background: lighten($rd-navbar-background, 10%);
      border: none;
      border-radius: 0;
      opacity: .2;
    }

    &::-webkit-scrollbar-track {
      background: $rd-navbar-background;
      border: none;
      border-radius: 0;
    }
  }

  // RD Navbar States
  &.active {
    .rd-navbar-nav {
      @include transform(translateX(0));
    }
  }

  &.rd-navbar--on-search {
      .brand-name {
        opacity: 0;
        visibility: hidden;
        @include transform(scale(0.7));
      }
  }

  .rd-navbar-fixed--visible {
    display: block;
  }

  .rd-navbar-fixed--hidden {
    display: none;
  }
}

html.rd-navbar-fixed-linked {
  body {
    padding-top: 55px;
  }
}