//
// Offsets
// --------------------------------------------------


// Insets
// -------------------------

$insets: (0, 10px ,15px , 55px , 70px , 150px);

@include indent-responsive(inset, left, padding-left, $medias, $insets);
@include indent-responsive(inset, right, padding-right, $medias, $insets);


// Elements
// -------------------------
.brand-slogan + *{  margin-top: 25px;}
h3  + p{margin-top: 25px;}
p  + .btn{margin-top: 20px;}
* + .list-index{  margin-top: 75px;}
h5 + p{  margin-top: 20px;}
.shell + .shell{  margin-top: 35px;}
img + *{  margin-top: 20px;}
* + .contact-info{margin-top: 30px;}
* + .list-inline{margin-top: 35px;}
p + p{margin-top: 5px;}

@media (min-width: $screen-md-min) {
  .brand-slogan + *{  margin-top: 45px;}
  p  + .btn{margin-top: 35px;}
  img + *{  margin-top: 40px;}

}
// Grid
// -------------------------


// Sections
// -------------------------


// Responsive
// -------------------------

$offsets: (-10px , 0, 4px, 10px , 15px , 20px , 25px , 30px , 40px , 50px , 60px , 70px , 110px);

html .page{
  @include indent-responsive(offset, top, margin-top, $medias, $offsets);
}
