//
// Blog post utility classes
// --------------------------------------------------

.blog-post{
  &-info{
    color: $color-nepal;
    font-size: 13px;
    font-weight: 700;

    li{
      display: block;

      li{
        margin-top: 10px;
      }
    }

    span,
    time{
      display: inline-block;
      vertical-align: middle;

      + span,
      + time{
        margin-left: 10px;
      }
    }
  }

  * + .blog-post-content{
    margin-top: 25px;
  }

  @media (min-width: $screen-xs-min) {
    .blog-post-info{
      li{
        display: inline-block;

        + li{
          margin-top: 0;
          margin-left: 10px;
        }
      }
    }
  }

  @media (min-width: $screen-md-min) {
    .border-md-left {
      padding-left: 24px;

      h3:before{
        left: -26px;
      }
    }

    .blog-post-info{
      li + li{
        margin-left: 30px;
      }
    }
  }
}

//
// Post utilities
// --------------------------------------------------

.post {

  * + .post-meta {
    margin-top: 24px;
  }

  * + .post-text {
    margin-top: 27px;
  }

  * + .btn {
    margin-top: 35px;
  }
}

.news-post {
  .post-body {
    padding: 35px 0 0;
  }
}

.post-single {
  * + .post-body {
    margin-top: 15px;
  }
}

.post + .post {
  margin-top: 60px;
}

@media (max-width: $screen-xs-max) {
  .news-post .post-image {
    max-height: 240px;
    overflow: hidden;
    img {
      position: relative;
      width: auto;
      height: auto;
      top: 30%;
      left: 20%;
      @include transform(translate(-20%, -30%));
    }
  }
}

@media (min-width: $screen-sm-min) {
  .news-post {
    .post-inner {
      min-height: 350px;
      @include display-flex;
      @include flex-direction(row);
      @include flex-wrap(nowrap);
      @include align-items(stretch);
      @include justify-content(space-between);

      .post-image {
        @include display-flex;
        @include flex-direction(column);
        @include align-items(center);
        overflow: hidden;

        img {
          height: auto;
          min-height: 240px;
          width: auto;
          max-width: none;
          @include flex-grow(1);
          flex-shrink: 0;
        }
      }

      .post-image {
        @include flex-grow(47);
        width: (100% - 59.83%);
      }

      .post-body {
        @include flex-grow(70);
        width: 59.83%;
      }

      * + .box-text {
        margin-top: 53px;
      }

      * + .divider {
        margin-top: 38px;
      }
    }
  }

  .post-left {
    .post-body {
      padding: 40px 0 60px 60px;
    }
  }

  .post-right {
    .post-body {
      padding: 40px 60px 60px 0;
    }

    .post-inner {
      @include flex-direction(row-reverse);
    }
  }

  .post-single {
    * + .post-body {
      margin-top: 32px;
    }
  }

  .post + .post {
    margin-top: 90px;
  }
}

@media (min-width: $screen-lg-min) {
  .post-left {
    .post-body {
      padding: 64px 0 60px 130px;
    }
  }

  .post-right {
    .post-body {
      padding: 60px 130px 60px 0;
    }
  }
}

@media (min-width: $screen-xl-min) {
  .post + .post {
    margin-top: 125px;
  }
}

