/*
* Fullwidth Layout
*/

.rd-navbar-fullwidth {
  display: block;

  &:after {
    content: '';
    background: $rd-navbar-background;
  }

  //RD Navbar Panel
  .rd-navbar-panel{
    position: relative;
    @include display-flex();
    @include justify-content(space-between);
    @include align-items(center);
    padding: $rd-navbar-fullwidth-panel-padding;

  }

  // RD Navbar Brand
  .rd-navbar-brand {
    margin-left: -10px;
    .brand-name {
      position: relative;
      display: inline-block;
      font-size: 46px;
      line-height: 1;

      color: $rd-navbar-color;

      &:hover,
      &:focus,
      &:active {
        color: $rd-navbar-color;
        outline: none;

        &:before{
          color:#75826b ;
        }
      }
      
      &:before{
        content: '\e064';
        font-family: 'fl-great-icon-set';
        display: inline-block;
        font-size: 42px;
        line-height: 1;
        vertical-align: bottom;
        color:#75826b ;
      }
    }
  }

  // RD Navbar Info
  .rd-navbar-info{
    a.icon:hover{
      transform:  rotateY(180deg);
    }

    .icon,
    a{
      display: inline-block;
      vertical-align: middle;
    }

    dl{
      position: absolute;
      top: 100%;
      right: 0;
      margin-top: 36px;
      z-index: 10;


      a{
        font-size: 18px;
        line-height: 1;
        letter-spacing: 0.03em;
      }

      dt{
        margin-right: 10px;
      }

      dd,dt{
        float: left;
      }
    }
  }

  // RD Navbar Nav
  .rd-navbar-nav {
    width: 100%;
    text-align: left;
    position: relative;

    &:after{
      content: '';
      position: absolute;
      display: block;
      height: 1px;
      top: -1px;
      left: -85px;
      right: -85px;
      border-bottom: 1px solid #46453f;
    }

    > li + li {
      margin-left: $navbar-fullwidth-nav-indent;
      @media (min-width: $screen-lg-min) {
        margin-left: 58px;
      }
    }

    > li {
      > a {
        position: relative;
        display: inline-block;
        overflow: hidden;
        padding: $rd-navbar-fullwidth-padding;
        color: $rd-navbar-fullwidth-nav-color;
        background: $rd-navbar-fullwidth-nav-background;
        line-height: $rd-navbar-fullwidth-line-height;
        font-size: $rd-navbar-fullwidth-font-size;
        
        &:after, 
        &:before{
          content: '';
          position: absolute;
          top: 0;
          display: block;
        }

        &:after{
          width: 10px;
          height: 10px;
          left: 50%;
          top: -10px;
          opacity: 0;

          transition: none;
          transition-delay: 0s;
          border: 10px solid transparent;
          border-top-color:$rd-navbar-fullwidth-nav-color ;
          @include transform(translateX(-50%));
        }

        &:before{
          background: $rd-navbar-fullwidth-nav-color;
          width: 0;
          height: 3px;
          opacity: 0;
          transition: width .3s;

        }
        
      }

      &.focus > a,
      > a:hover,
      &.active > a {
        color: $rd-navbar-fullwidth-nav-hover-color;
        background: $rd-navbar-fullwidth-nav-hover-background;

        &:before{
          width: 100%;
          opacity: 1;
        }
        &:after{
          opacity: 1;
          top: 0;

          transition: .2s;
          transition-delay: .2s;
        }
      }
    }

    > .rd-navbar-submenu {
      > ul {
        box-shadow: $rd-navbar-shadow;
      }
      // Submenu Arrow
      .rd-navbar-submenu .rd-navbar--has-dropdown {
        position: relative;

        .rd-navbar-submenu-toggle {
          color: $rd-navbar-fullwidth-toggle-color;
          position: absolute;
          display: inline-block;

          right: 15px;
          top: 50%;
          @include transform (translateY(-50%));

          &:before {
            content: '\e315';
            font-family: 'Material Icons';
          }
        }
      }

      .rd-navbar-submenu.focus > .rd-navbar-dropdown {
        display: block;
      }
    }
  }

  // RD Navbar Inner
  .rd-navbar-inner {
    max-width: $rd-navbar-fullwidth-width;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
  }


  // Media styles
  @media (max-height: 900px) {
    //RD Navbar Panel
    .rd-navbar-panel{
      padding: 25px 0;
    }

    // RD Navbar Nav
    .rd-navbar-nav {
      > li > a{
        padding: 23px 5px 14px 5px;
      }
    }

    // RD Navbar Info
    .rd-navbar-info{
      dl{
        margin-top: 16px;
      }
    }
    }

  // RD Navbar States
  &.rd-navbar--is-stuck{
    .rd-navbar-panel{
      padding: 0;
      transition: none;

      .list-inline{
        display: none;
      }

      > *:not(.rd-navbar-info){
        display: none;
      }

      .rd-navbar-info dl{
        margin-top: 16px;
      }
    }

    .rd-navbar-nav > li > a{
      padding: 25px 5px 15px 5px;
    }
  }
}