//
// Scaffolding
// --------------------------------------------------

// Reset fonts for relevant elements
input,
button,
select,
textarea {
  outline: none;
}

// Page styles
// --------------------------------------------------

.page {
  overflow: hidden;
  background-color: $content-bg;
}

// Header styles
// --------------------------------------------------

.page-head {
  background-color: $header-bg;

  .rd-parallax .section-cover {
    min-height: calc(100vh - 67px);
    @include display-flex();
    @include flex-direction(column);
    @include justify-content(center);
    @include align-items(center);
  }

  .content-middle{
    position: absolute;
    top: 50%;
    left: 50%;
    @include transform(translate(-50% , -50%));
  }

  .brand-slogan {
    position: relative;
    padding: 32px 40px;
    border: 1px solid #c3c0bf;

    border-bottom-width: 0;

    &:before,
    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 1px;
      border-top: 1px solid #c3c0bf;
    }

    &:before {
      left: 0;
    }

    &:after {
      right: 0;
    }

    .slogan-text-bottom{
      margin-top: 20px;
    }


    @media (min-width: $screen-xs-min) {
      .slogan-text-bottom {
        position: absolute;
        bottom: 0;
        left: 50%;
        display: inline-block;
        width: 100%;
        margin-top: 0;
        @include transform(translate(-50%, 50%));
      }

      &:before,
      &:after {
        width: 10%;
      }
    }

    @media (min-width: $screen-sm-min) {
      &:before,
      &:after {
        width: 82px;
      }
    }
  }
}

// Footer styles
// --------------------------------------------------

.page-footer {
  color: #cdcdcd;

  a:not(.btn){
    color: #cdcdcd;

    &:hover,
    &:focus{
      color: $brand-primary;
    }
  }

  .contact-info{
    letter-spacing: .1em;
  }

  .copyright{

    font-size: 13px;
    font-weight: 700;
    letter-spacing: 0.1em;
    color: $gray-light-1;

    a:not(.btn){
      color: $gray-light-1;

      &:hover,
      &:focus{
        color: $brand-primary;
      }
    }
  }
}
// Links

a {

  &:hover,
  &:focus {
    outline: none;
  }
}

// Horizontal rules

hr {
  margin-top: 0;
  margin-bottom: 0;
  border-top: 1px solid $hr-border;
}

