//
// Custom Thumbnails
// --------------------------------------------------

.tablet ,
.mobile{
  .thumb{

    img{
      opacity: 0.6;
    }

    &:before{
      opacity: 1;

      @include transform(translate(-50% , -50%) rotate(0deg));
      z-index: 1;
    }
  }
}

.thumb{
  position: relative;
  display: block;
  background: $color-ebony-clay;
  //overflow: hidden;
  &:before{
    content: '\e8ff';
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 48px;
    line-height: 1;
    color: $color-nepal;
    font-family: 'Material Icons';
    transition: .3s;
    opacity: 0;
    z-index: 1;

    @include transform(translate(-50% , -50%) rotate(180deg));
  }

  img{
    transition: .3s;
    opacity: 0.45;
    width: 100%;
    height: auto;
    display: block;
    margin: -1px;
  }

  &:hover{
    img{
      opacity: 0;
    }

    &:before{
      opacity: 1;
      @include transform(translate(-50% , -50%) rotate(0deg));
    }
  }
}

.thumbnail{
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 0;

  .caption{
    padding: 0;
    color: $gray-base;
  }
}

.img-thumbnail,
.thumbnail{
  box-shadow: none;
}

// Make thumbnails to fit entire container width
.thumbnail-block {
  display: block;

  > img,
  a > img {
    width: 100%;
    height: auto;
  }
}