//
// Comment utilities
// --------------------------------------------------
.comment {

  figure {
    img {
      width: auto;
      height: auto;
    }
  }

  .comment-body {
    @include display-flex;
    @include flex-direction(row);
    @include flex-wrap(wrap);
    @include justify-content(space-between);
    @include flex-grow(1);
    height: 100%;
  }

  .comment-meta {
    $value: 8px;

    position: relative;
    display: block;
    width: 100%;
    margin: 15px 0 0 0;
    @include transform(translateY(-$value));
    vertical-align: middle;
    color: $color-nepal;
    font-family: $font-family-sec;

    .link{
      color: $color-nepal;

      &:hover,
      &:focus,
      &:active{
        color: $brand-primary;
      }
    }

    > * {
      display: inline-block;
      margin-top: $value;
      font-size: 13px;
      line-height: 20px;
      vertical-align: middle;
      font-weight: 900;
    }

    > a {
      position: relative;
    }

  }

  .unit-body {
    margin-top: -5px;
    width: 100%;
  }
}

.post-comment {

}

.comment-group-reply {
  padding-left: 6%;

}

// Elements
.comment-body {

}

.comment-footer {
  @include display-flex;
  @include flex-direction(row);
  @include justify-content(space-between);
  margin-top: 25px;
}

// Offsets
* + .comment {
  margin-top: 30px;
}

* + .post-comment {

}

// Adaptive
@media (min-width: $screen-xs-min) {
  .comment {
    .comment-meta {
      margin-top: -14px;
      margin-bottom: -13px;
      @include align-self(flex-end);
      text-align: right;
    }
  }
}

@media (max-width: $screen-xs-max) {
  .comment {
    .comment-meta {
      > *:not(:last-child) {
        margin-right: 7px;
      }
    }
  }
}

@media (min-width: $screen-sm-min) {
  .comment {
    .comment-meta {
      *:first-child{
        margin-right: 15px;
      }
      > *:not(:first-child) {
        margin-left: 27px;
      }
    }
  }
}

@media (min-width: $screen-md-min) {
  .comment {

  }
}
