//
// Icons
// --------------------------------------------------

// Base styles
// --------------------------------------------------

.icon {
  transition: .3s;
  display: inline-block;
  text-align: center;


  &:before {
    position: relative;
    display: inline-block;
    font-weight: 400;
    font-style: normal;
    speak: none;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}


// Alternate icons
// --------------------------------------------------

.icon-white {
  color: $white;
}

.icon-nepal {
  color: $color-nepal;
}

.icon-primary {
  background: $brand-primary;
  color: $white;

}

.icon-roman {
  color: $color-roman;

}

html .page{
  a.icon-primary{
    color: $white;
    &:hover,
    &:focus,
    &:active{
      color: $white;
      background: darken($brand-primary, 15%);
    }
  }

  a.icon-roman {
    color: $color-roman;

    &:hover{
      color: $color-ebony-clay;
    }
  }
}


  // Button Sizes
// --------------------------------------------------

.icon-xs {
  font-size: 20px;
  line-height: 13px;
}

.icon-sm {
  font-size: 24px;
  line-height: 41px;
  padding-left: 5px;
  padding-right: 5px;
  min-width: 43px;
}

.icon-lg {
  font-size: 36px;
  line-height: 36px;
}