/*
* Static Layout
*/

.rd-navbar-static {
  display: block;
  padding: 13px 0;
  border-bottom: 1px solid #2c3540;


  // RD Navbar nav wrap
  .rd-navbar-nav-wrap {
    width: 100%;
    text-align: center;
    opacity: 0;
    visibility: hidden;
    transition: all .5s;
    &.active{
      opacity: 1;
      visibility: visible;
    }
  }



  // RD Navbar  Inner
  .rd-navbar-inner{
    @include display-flex();
    @include justify-content(space-between);
    @include align-items(center);

  max-width: $rd-navbar-fullwidth-width;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
  }

  // RD Navbar Toggle
  .rd-navbar-toggle{
    display: block;
    position: absolute;
    right: 0;
    height: 48px;
    top: 50%;
    @include transform(translateY(-50%));
  }

  .rd-navbar-nav{
    padding-right: 50px;
    text-align: right;
    > li {
      display: inline-block;
      vertical-align: middle;

      > a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;
        text-transform: uppercase;
        font-family: $font-family-base;
        letter-spacing: 0.1em;
        padding: $rd-navbar-static-padding;
        color: $rd-navbar-static-nav-color;
        background: $rd-navbar-static-nav-background;
        line-height: $rd-navbar-static-line-height;
        font-size: $rd-navbar-static-font-size;
        border: 1px solid transparent;
        transition: .3s;
      }

      &.focus > a,
      > a:hover,
      &.active > a {
        color: $rd-navbar-static-nav-hover-color;
        background: $rd-navbar-static-nav-hover-background;
        border-color: rgba($white, .63);
      }

      & + li{
        margin-left: $navbar-static-nav-indent;
      }
    }
  }

  @media (min-width: $screen-lg-min) {
    // RD Navbar brand
    .rd-navbar-brand{
      margin-left: 15px;
    }

    // RD Navbar Panel
    .rd-navbar-panel{
        padding-left: 15px;
    }
  }
}

