//
// Custom typography
// --------------------------------------------------

// Page
// -------------------------

.page {
  overflow: hidden;
}

// Page header
// -------------------------

.page-header {
}

// Fonts
// -------------------------

.font-default {
  font-family: $font-family-base;
}

.font-sec {
  font-family: $font-family-sec;
}

// Basic Typography
// -------------------------

#{headings()} {
  margin-top: 0;
  margin-bottom: 0;
  letter-spacing: 0.1em;
}

h1,
.h1 {
  font-size: 32px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h1;
    font-size: $font-size-h1;
  }
}

h2,
.h2 {
  font-size: 30px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h2;
    font-size: $font-size-h2;
  }
}

h3,
.h3 {
  font-size: 28px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h3;
    font-size: $font-size-h3;
  }
}

h4,
.h4 {
  font-size: 24px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h4;
    font-size: $font-size-h4;
  }
}

h5,
.h5 {
  font-size: 20px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h5;
    font-size: $font-size-h5;
  }
}

h6,
.h6 {
  font-size: 17px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h6;
    font-size: $font-size-h6;
  }
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  a {
    transition: .3s all ease;
  }

  a:hover {
    color: $brand-primary;
  }

  .small {
    color: $gray-dark;
  }
}

p,
.p,
.list {
  a:hover {
    text-decoration: underline;
  }
}

.big {
  font-size: 16px;
  line-height: 1.32;
}

@media (min-width: $screen-xs) {
  .big {
    font-size: 16px;
    line-height: 1.32;

    h1 &,
    .h1 & {
      font-size: 142.22%;
      line-height: 1.125;
    }

    h2 &,
    .h2 & {
      font-size: 147.36%;
      line-height: 1.142857142857143;
    }
  }
}

small,
.small {
  font-size: $font-size-small;
  line-height: floor(($font-size-small * $line-height-small));
}

blockquote small,
blockquote .small,
small,
.small {
  font-size: 93.75%;

  h1 &,
  .h1 & {
    font-size: 62.22222222222222%;
  }

  h2 &,
  .h2 & {
    font-size: 63.15789473684211%;
  }

  h3 &,
  .h3 & {
    font-size: 73.33333333333333%;
  }

  h4 &,
  .h4 & {
    font-size: 75%;
  }

  h5 &,
  .h5 & {
    font-size: 72.72727272727273%;
  }

  h6 &,
  .h6 & {
    font-size: 94.11764705882353%;
  }
}

code {
  padding: 5px 7px;
  font-size: 75%;
  color: $brand-danger;
  background-color: $gray-lighter;
  border-radius: 2px;
}

mark, .mark {
  background-color: $brand-primary;
  padding: .1em;
}

// Lists
// -------------------------

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.list {
  > li + li {
    margin-top: 10px;
  }
}

// List inline
//

.list-inline {
  margin-left: -5px;
  margin-right: -5px;

  > li {
    padding-left: 0;
    padding-right: 1px;
  }

  @media (min-width: $screen-lg-min) {
    > li {
      padding-left: 6px;
      padding-right: 6px;
    }
  }
}

// Description Lists
dl {
  margin: 0;
}

// List terms
//

.list-terms {
  margin-top: 28px;
  margin-bottom: 28px;
  color: $gray-base;
  dt + dd {
    margin-top: 6px;
  }
  dd + dt {
    margin-top: 30px;
  }
}

// List index
//

.list-index {
  counter-reset: li;

  li {

    position: relative;
    &:after{
      content: '';
      height: 1px;
      width: 90px;
      display: block;
      position: absolute;
      top: 60px;
      left: 50%;
      @include transform(translateX(-50%));
      background: rgba($white, .38);
    }

    @media (min-width: $screen-md-min) {
      &:after{
        top: 7px;
        bottom: 7px;
        left: 70px;
        width: 1px;
        height: auto;
        @include transform(translateX(0));
      }
    }


    .list-index-counter {
      position: relative;
      display: inline-block;
      &:before {
        content: counter(li, decimal-leading-zero);
        counter-increment: li;
        display: inline-block;
        font-size: 36px;
        line-height: 1;
        color: $white;
        font-weight: 700;
      }


    }

    + li{
      margin-top: 75px;
    }
  }
}

// List marked
//

.list-marked {
  li {
    position: relative;
    padding-left: 16px;

    &:before {
      content: '';
      display: inline-block;
      background: $gray-dark;
      width: 3px;
      height: 3px;
      position: absolute;
      left: 0;
      top: 50%;
      @include transform (translateY(-50%));
    }
  }
}

// Misc
// -------------------------

// Blockquotes
blockquote {
  q {
    &:before,
    &:after {
      content: none;
    }
  }
  cite {
    font-style: normal;
  }
}

// Addresses
address {
  margin-top: 0;
  margin-bottom: 0;
}

// Backgrounds
// -------------------------

// Image backgrounds
//

.bg-image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  &-1 {
    background-image: url(../images/home-4-1679x1190.jpg);
  }

  &-2 {
    background-image: url(../images/about-2-1680x380.jpg);
  }
}

.bg-fixed {
  @media (min-width: $screen-lg-min) {
    background-attachment: fixed;
  }
}


// Contextual backgrounds
//
.bg-gray-base {
  @include bg-variant-custom(#{$gray-base});
}

.bg-gray {
  @include bg-variant-custom(#{$gray});
}

.bg-white {
  @include bg-variant-custom(#{$white});

   #{headings()}{
      color: $gray-dark;
    }
  
  a{
    color: $color-ebony-clay;
    
    &:hover,
    &:focus,
    &:active{
      color: $brand-primary;
    }
  }

  .border-md-left {
    h3:before{
      background: rgba($gray-dark, .5);
    }
  }

  color: $gray-light;
}

.bg-ebony-clay {
  @include bg-variant-custom(#{$color-ebony-clay});
}

.bg-oxford-blue {
  @include bg-variant-custom(#{$color-oxford-blue});
}

// Colors
// -------------------------

// Contextual colors
//

.page {
  @include text-emphasis-variant('.text-primary', $brand-primary);
  @include text-emphasis-variant('.text-dark', $gray-dark);
  @include text-emphasis-variant('.text-gray', $gray);
  @include text-emphasis-variant('.text-white', $white);
  @include text-emphasis-variant('.text-nepal', $color-nepal);
}

// Page footer
// -------------------------

.page-footer {

}